import { Injectable } from '@angular/core';

declare var esptouch: any;
@Injectable({
  providedIn: 'root'
})
export class VhEspSmartConfig {

  constructor() { }

  public startESP(wifiInfo: { ssid: string, bssid: string, password: string }) {
    return new Promise<{ bssid: string, ip: string } | string>((resolve, rejects) => {
      esptouch.start(wifiInfo['ssid'], wifiInfo['bssid'], wifiInfo['password'], "1", "1",
        (success: any) => {
          if (success) resolve(success)
          else rejects("Not found")
        }, (error: any) => {
          rejects(error)
        })
    })
  }

  public stopESP() {
    return new Promise<any>((resolve, rejects) => {
      esptouch.stop(res => resolve(res), err => rejects(err))
    })
  }
}
